.user-profile {
  display: flex;
  &__item {
    margin: 20px;
    span {
      font-weight: bold;
    }
  }
  &__image {
    border-radius: 20px;
    img {
      width: 200px;
      height: 200px;
      border-radius: 10px;
      object-fit: cover;
    }
  }
}

.user-info {
  background: rgb(211, 211, 211);
  width: 100%;
  border-radius: 10px;
  margin: 0 20px;
}
